<template>
  <div>
    <div class="line"></div>
    <div class="banner-slider" v-if="banner.length>0">
      <swiper :options="swiperOption">
        <!--  -->
        <swiper-slide v-for="banner in banner" :key="banner.id">
          <a :href="banner.targetUrl">
            <img :src="banner.imgUrl"  alt="">
          </a>
        </swiper-slide>
        <!-- ... -->
        <!-- slot -->
        <div class="swiper-pagination"  slot="pagination" v-if="banner.length>1">
          <!---->
          <!--<span class="swiper-pagination-bullet"><span>-->
        </div>
      </swiper>
  </div>

  </div>
</template>
<script>
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  export default{
    components:{swiper,swiperSlide},
    //props:['banner'],
    data(){
      return{
        // 
//        banner:[{imgUrl:"",targetUrl:'https://jf.trc.com/credit_h5'}],
        swiperOption: {
          // swiper
          name: 'currentSwiper',
          // Swiper
          autoplay: 3000,
          loop:true,//
          direction : 'horizontal',//vertical
          paginationType : 'bullets',//
          autoplayDisableOnInteraction:false,//
          grabCursor : false,//SwipertrueSwiper
          setWrapperSize :true,
          autoHeight: true,
          pagination : '.swiper-pagination',
          paginationClickable :true,
          mousewheelControl : false,
          observeParents:true,
          effect:'fade',
          // if you need use plugins in the swiper, you can config in here like this
          debugger: true,
        }
      }
    },
    mounted (){

    },
    props: ['banner']
  }
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
  @function p2r ($px){
    @return $px/75 + rem;
  }
  .swiper-pagination{
    color: #fff;
    .swiper-pagination-bullet {
      background: #000;
    }
  }
  .swiper-container{
    background-color: #fff;
  }
  .swiper-slide{
    text-align: center;
    a{
      display: block;
    }
    img{
      width:100%;
      height: p2r(173);
      border: none;
    }
  }
  .line{
    width: p2r(660);
    padding: p2r(0) p2r(4);
    height: 1px;
    background-color: #e4e4e4;
    margin:0 auto;
  }

</style>



