<template>
  <div class="banner-slider">
    <swiper :options="swiperOption">
      <!--  -->
      <swiper-slide v-for="banner in banner" :key="banner.id">
        <!--<a :href="banner.targetUrl" target="_blank">-->
          <img :src="banner.fileUrl+'?imageView2/2/h/500'"  alt="">
        <!--</a>-->
      </swiper-slide>
      <!-- ... -->
      <!-- slot -->
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>
<script>
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  export default{
    components:{swiper,swiperSlide},
    //props:['banner'],
    data(){
      return{
        // 
        banner:[{fileUrl:"https://jfimg.tairanmall.com/FhQc2jGHtSfjyL4s3fM0xydFdug9"}],
        swiperOption: {
          // swiper
          name: 'currentSwiper',
          // Swiper
          autoplay: 3000,
          loop:true,//
          direction : 'horizontal',//vertical
          paginationType : 'bullets',//
          autoplayDisableOnInteraction:false,//
          grabCursor : false,//SwipertrueSwiper
          setWrapperSize :true,
          autoHeight: true,
          pagination : '.swiper-pagination',
          paginationClickable :true,
          mousewheelControl : false,
          observeParents:true,
          effect:'fade',
          // if you need use plugins in the swiper, you can config in here like this
          debugger: true,
        }
      }
    }
  }
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
  @function p2r ($px){
    @return $px/75 + rem;
  }
  .swiper-container{
    background-color: #fff;
    box-sizing: border-box;
    padding-top: p2r(61);
    height: p2r(730);
    border:1px solid #E4E4E4;
    width: p2r(750);
    overflow: hidden;
    z-index: 0;
    .swiper-wrapper{
      overflow: hidden;
    }
  }
  .swiper-slide{
    width: p2r(750);
    text-align: center;
    img{
      width: p2r(560);
      height: p2r(560);
    }
  }
  .swiper-pagination{
    bottom: p2r(40);
  }
</style>



