<template>
  <div id="app">
    <transition>
      <router-view></router-view>
    </transition>
  </div>
</template>
<script>
  import mall_home from './views/mall_home/mall_home'//
  import goods_detail from './views/goods_detail/goods_detail'//
  import con_record from './views/con_record/con_record'//
  import address_list from './views/address_list/address_list'//
  import address_add from './views/address_add/address_add'//
  import address_edit from './views/address_edit/address_edit'//
  import order_list from './views/order_list/order_list'//
  import problems from './views/problems/problems'//
  import order_end from './views/order_end/order_end'//
  import order_detail from './views/order_detail/order_detail'//
  import order from './views/order/order'//
  import logistics from './views/logistics/logistics'//
  import activity from './views/activity/activity'//
  import goods from './views/goods/goods'//
  import personal from './views/personal/personal'//
  /**/
  import daily from './views/daily/daily'//
  import login from './login'//
  export default {
    name: 'app',
    components: {
      mall_home,
      goods_detail,
      con_record,
      address_list,
      address_add,
      address_edit,
      order_list,
      order_detail,
      problems,
      logistics,
      order_end,
      order,
      activity,
      goods,
      personal,
			daily,
      login
    }
  }
</script>
<style lang="sass">
  @import "./assets/css/reset.css";
  @import "./assets/css/common.css";
</style>



